.main-menu > div {
    background: black;
    color: white;
}

.main-menu .modal-body {
    padding: 0;
}

.main-menu > div div {
    background: inherit;
    color: inherit;
}

.menu-item {
    padding-top: 0.3rem;
}

.menu-item.row {
    padding-top: 0.3rem;
    margin-left: 0;
    margin-right: 0;
    padding-right: 1.2rem;
}

.menu-item:first-of-type {
    padding-top: 1.3rem;
}

.menu-item:last-of-type {
    padding-bottom: 1.3rem;
}

.menu-item:hover {
    cursor: pointer;
    background: white;
    color: black;
}

.menu-item.disabled {
    opacity: 0.65;
    cursor: not-allowed;
}


.menu-item .right {
    border-top: 1px solid white;
    padding-top: 0.3rem;
}

.menu-item .title, .menu-item .badge {
    font-size: 1.5rem;
    /*text-transform: uppercase;*/
}

.menu-item .badge {
    font-size: 1.25rem;
    min-width: 3rem;
    /*text-transform: uppercase;*/
}

.menu-item:not(:last-of-type) .description {
    min-height: 3rem;
    /*border-bottom: 1px solid white;*/
}

.menu-item .right .col {
    padding-left: 0;
}

.menu-item .left .figure {
    margin-bottom: 0;
}

/* 576 is boostrap's sm */
@media (max-width: 576px) {
    .main-menu .modal-dialog {
        top: 10vh;
        max-height: 80vh;
    }
}
