/* 576 is boostrap's sm */
@media (max-width: 576px) {
    .footer {
        position: absolute;
        width: 100vw;
        background: #4f6aab;
        padding: 0.7rem 0.3rem;
        bottom: 0;
    }

    .footer > div {
        padding: 0;
    }

    .footer > div > * {
        width: 100%;
    }

    .footer img {
        width: 2.5em;
        height: 2.5em;
        margin-left: auto;
        margin-right: auto;
    }

    .footer img:hover {
        animation: bulge-anim;
    }

    .footer .disabled {
        opacity: 0.5;
    }
}

.footer-play-button {
    position: absolute;
    width: 100vw;
    bottom: 2.7rem;
    z-index: 1;
}

.footer-play-button > * {
    margin-left: auto;
    margin-right: auto;
}

.footer-play-button button {
    font-size: larger;
    border-width: 5.4px;
    border-color: #a4c1b3;
}

.footer-play-button button:disabled {
    font-size: initial;
    background-color: white !important;
}

