.hr {
    background: linear-gradient(180deg,
    rgba(0,0,0,0) calc(50% - 1px),
    rgba(192,192,192,1) calc(50%),
    rgba(0,0,0,0) calc(50% + 1px)
    );
}

.goals-header {
    margin-top: 0.3rem;
}

.goals-title {
    color: #adb4c6;
    text-transform: uppercase;
}

.goal > div {
    background: #e1e6e5;
    padding: 0.5rem 0.5rem 0.1rem;
    border-radius: 1rem;
    text-align: center;
    height: 100%;
}

.bonus-circle {
    border-radius: 3rem;
    color: white;
    font-weight: 600;
    font-size: 1.6rem;
    background: #a8b3d3;

    line-height: 3.5rem;
    height: 3.5rem;
    width: 3.5rem;
}

/* 576 is boostrap's sm */
@media (max-width: 576px) {
    .goal {
        padding: 1vw 1vw 0.1rem;
        font-size: small;
    }
    .bonus-circle {
        font-size: 1rem;
        line-height: 2.5rem;
        height: 2.5rem;
        width: 2.5rem;
    }
}
