.invite .practice a {
    text-decoration: underline;
    opacity: 0.5;
}

.invite .qr {
    width: 300px;
    height: 300px;
}
.invite canvas {
    width: 100% !important;
    height: 100% !important;
}

/* 576 is boostrap's sm */
@media (max-width: 576px) {
    .invite .modal-dialog {
        background: #000000B0;
        height: 100vh;
        overflow: hidden;
    }

    .invite .modal-dialog div {
        background: transparent;
        color: white;
        text-align: center;
    }

    .invite .top-spacer {
        height: 20vh;
    }

    .invite .qr {
        width: 50vw;
        height: 50vw;
    }

    .invite input {
        width: 60vw;
    }
}

@media (max-height: 576px) {
    .invite .top-spacer {
        height: 10vh;
    }
}

